import React, {Component} from 'react';
import {connect} from 'react-redux';

import Card from '../../components/Card/Card';
import classes from './Albums.module.css'
import * as actions from '../../store/actions/actions';
import Spinner from '../../components/UI/Spinner/Spinner';
import musicPlaceholder from '../../assets/img/music-placeholder.jpg';

class Albums extends Component {
    state = {
        name: null,
        page: 1,
        limit: 20,
        albumsToShow: 10
    };

    componentDidMount() {
        this.setState(
            {
                name: decodeURI(this.props.location.search.substr(1))
            }
        );
        this.props.onAlbumsInit(decodeURI(this.props.location.search.substr(1)), this.state.page, this.state.limit);
    }

    onClickHandler = (link) => {
        window.open(link, "_blank")
    };

    onSelectHandler = (event) => {
        const selectAlbums = this._selectEl;
        selectAlbums.value = event.target.value;

        this.setState({
            albumsToShow: event.target.value
        });
    };

    render() {
        let albums = this.props.loading ? <Spinner/> : (
            this.props.albums.slice(0, this.state.albumsToShow).map((album) => {
                return <Card
                    key={Math.random()}
                    src={album.src ? album.src : musicPlaceholder}
                    title={album.name !== '(null)' ? album.name : 'Not Found'}
                    clicked={() => {
                        this.onClickHandler(album.link)
                    }}/>
            })
        );

        return (
            <div className={classes.AlbumsContainer}>
                <h2 className={classes.Subheading}>{this.state.name ? `${this.state.name} Top Albums` : 'Not Found'}</h2>
                <div className={classes.SelectWrapper}>
                    <select
                        className={classes.AlbumsSelector}
                        onChange={(event) => {
                            this.onSelectHandler(event)
                        }}
                        ref={(node) => {
                            this._selectEl = node
                        }}
                        value={this.state.albumsToShow}
                        aria-label="Albums amount">
                        <option value="10">10</option>
                        <option value="15">15</option>
                        <option value="20">20</option>
                    </select>
                </div>
                {albums}
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        albums: state.albums,
        loading: state.loading
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onAlbumsInit: (name, page, limit) => {
            dispatch(actions.fetchAlbumsInit(name, page, limit))
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Albums);