import React, {Component} from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import {withRouter} from 'react-router-dom';

import classes from './Header.module.css';
import * as actions from '../../store/actions/actions';

class Header extends Component {
    state = {
        searchValue: ''
    };

    inputChangedHandler = (event) => {
        this.setState({
            searchValue: event.target.value
        });
    };

    onSubmitHandler = (event) => {
        event.preventDefault();
        if(this.props.location !== '/') {
            this.props.history.replace('/');
        }
        this.props.onArtistSearchInit(this.state.searchValue)
    };

    render() {
        return (
                <header className={classes.Header}>
                    <div className={classes.HeaderContainer}>
                        <Link to="/" onClick={this.props.onArtistsInit} className={classes.HomeLink}>Top Artists</Link>
                        <form
                            className={classes.SearchForm}
                            onSubmit={(event) => {
                                this.onSubmitHandler(event)
                            }}>
                            <div role="search" className={classes.SearchField}>
                                <input
                                    aria-label="search"
                                    placeholder="Search..."
                                    value={this.state.searchValue}
                                    onChange={(event) => {
                                        this.inputChangedHandler(event)
                                    }}/>
                            </div>
                        </form>
                    </div>
                </header>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onArtistSearchInit: name => {
            dispatch(actions.fetchArtistInfoInit(name))
        },
        onArtistsInit: () => {
            dispatch(actions.fetchArtistsInit())
        }
    }
};

export default withRouter(connect(null, mapDispatchToProps)(Header));